const projectModalFullStack = document.querySelector('#project-about');
const projectModalWeddingProject = document.querySelector('#project-contacts');
const projectModaProTest = document.querySelector('#project-contacts-two');

const projectOpenBtnFullStack = document.querySelector('#project-about-btn');
const projectOpenBtnWeddingProject = document.querySelector('#project-contacts-btn');
const projectOpenBtnProTest = document.querySelector('#project-contacts-two-btn');


const projectModals = [ projectModalFullStack, projectModalWeddingProject, projectModaProTest ];
const projectBtns = [ projectOpenBtnFullStack, projectOpenBtnWeddingProject, projectOpenBtnProTest ];

projectBtns.forEach((btn, index) => {
    const projectModal = projectModals[index];

    if (btn) {
        btn.addEventListener('click', (event) => {
            event.preventDefault();
            projectModal.classList.add(MODAL_ACTIVE_CLASS);

            document.body.classList.add(BODY_SCROLL_DISABLE_CLASS);
        })
    }
});